@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
.articles {
  display: flex;
  justify-content: center;
  padding: 1em 0.5em 4em 0.5em; }

/* colors */
.card {
  width: 100%;
  padding: 1em 0.7em;
  border-radius: 0.5em;
  box-shadow: 3px 3px 17px rgba(0, 0, 0, 0.1);
  margin: 0.7em 0; }
  .card__header {
    display: flex;
    justify-content: space-between;
    padding: 1em 0;
    font-family: sans-serif; }
  .card__title {
    color: #1292b4;
    cursor: pointer; }
    .card__title h5 {
      font-size: 1.2em;
      font-weight: 400;
      margin: 0; }
    .card__title:hover {
      opacity: 0.7; }
  .card__text {
    line-height: 1.7;
    word-break: break-word; }
  .card__icon {
    display: flex;
    justify-content: center;
    align-items: center; }
    .card__icon span {
      padding-right: 0.4em; }
  .card__footer {
    font-size: 0.8em;
    color: #afafaf;
    padding: 0.5em 0; }

/* colors */
.jumbotron {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2em 1em; }
  .jumbotron__title {
    padding-top: 3em;
    font-size: 3em;
    font-weight: 400; }
  .jumbotron__text {
    color: #000;
    font-size: 1.3em;
    line-height: 1.7;
    letter-spacing: 0.01em;
    font-weight: 400; }

